.card {
    padding: 5px;
    background-color: rgba(255, 255, 255, 0.8);
    /* Transparent card */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}

.display-weather .row {
    margin: 0;
    padding: 0;
}

.display-weather {
    margin-top: 10px;
    width: 100%
}

.suggestion-list {
    max-height: 28rem;
}

.card-title {
    font-size: 20px;
    font-weight: bold;
}

.text-success {
    background: rgb(5, 198, 86);
    background: linear-gradient(90deg, rgba(5, 198, 86, 1) 0%, rgba(46, 222, 55, 1) 45%, rgba(44, 214, 112, 1) 100%);
    color: white;
}

.temp .temp-gif {
    width: 150px;
}

.temp {
    display: flex;
    justify-content: center;
    align-items: center;
}

.celsius {
    font-size: larger;
    font-weight: 400;
}

@media screen and (max-width:700px) {
    .temp {
        display: grid;
        flex-direction: column;
        text-align: center;
    }

    .temp .temp-gif {
        margin-bottom: 10px;
    }
}

.details-list{
    width: 50px;
}

.sub-head{
    font-size: 12px;
}

.sub{
    font-size: 10px;
}

.air-quality{
    justify-self: center
}